import React, { useEffect, useState } from 'react';
import { LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer } from 'recharts';

export default function ChartSection({ selectedStock }) {
  const [chartData, setChartData] = useState([]);
  const [activeFilter, setActiveFilter] = useState('1D');
  const API_KEY = 'ZNF4S20RSWYWQ8X1';

  useEffect(() => {
    const fetchChartData = async () => {
      try {
        const response = await fetch(
          `https://www.alphavantage.co/query?function=TIME_SERIES_DAILY&symbol=${selectedStock}&apikey=${API_KEY}`
        );
        const data = await response.json();
        const series = data['Time Series (Daily)'];
        
        if (series) {
          const dates = Object.keys(series).reverse().slice(-30);
          const prices = dates.map(date => ({
            date: date.slice(5),
            price: parseFloat(series[date]['4. close'])
          }));
          setChartData(prices);
        }
      } catch (error) {
        console.error('Error fetching chart data:', error);
      }
    };

    fetchChartData();
  }, [selectedStock]);

  return (
    <section className="card chart-section">
      <div className="chart-header">
        <h2>Price Chart</h2>
        <div className="chart-filters">
          {['1D', '5D', '1M', '6M', '1Y', 'MAX'].map(filter => (
            <button
              key={filter}
              className={`filter-btn ${activeFilter === filter ? 'active' : ''}`}
              onClick={() => setActiveFilter(filter)}
            >
              {filter}
            </button>
          ))}
        </div>
      </div>
      <div style={{ width: '100%', height: '350px' }}>
        {chartData.length > 0 ? (
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={chartData}>
              <CartesianGrid strokeDasharray="3 3" stroke="rgba(255,255,255,0.1)" />
              <XAxis dataKey="date" stroke="#B1B5C3" style={{ fontSize: '12px' }} />
              <YAxis stroke="#B1B5C3" style={{ fontSize: '12px' }} />
              <Tooltip 
                contentStyle={{ backgroundColor: '#1a1a1c', border: '1px solid #3b82f6' }}
                labelStyle={{ color: '#B1B5C3' }}
              />
              <Line type="monotone" dataKey="price" stroke="#3b82f6" dot={false} isAnimationActive={false} />
            </LineChart>
          </ResponsiveContainer>
        ) : (
          <div style={{ display: 'flex', justifyContent: 'center', alignItems: 'center', height: '100%', color: '#7d8590' }}>
            Loading chart data...
          </div>
        )}
      </div>
    </section>
  );
}
