import React, { useState, useEffect } from 'react';
import { LineChart, Line, ResponsiveContainer } from 'recharts';

export default function Market() {
  const [activeTab, setActiveTab] = useState('View All');
  const [marketData, setMarketData] = useState([]);
  const [chartDataMap, setChartDataMap] = useState({});

  const stocks = [
    { rank: 1, name: 'Apple', symbol: 'AAPL', price: 232.14, change: -0.19, market: '$3.01T' },
    { rank: 2, name: 'Microsoft', symbol: 'MSFT', price: 431.75, change: 0.62, market: '$3.40T' },
    { rank: 3, name: 'Tesla', symbol: 'TSLA', price: 245.50, change: 1.12, market: '$780.2B' },
    { rank: 4, name: 'Reliance', symbol: 'RELIANCE', price: 2755.00, change: -0.85, market: '‚Çπ18.7T' },
    { rank: 5, name: 'Infosys', symbol: 'INFY', price: 1718.40, change: 0.67, market: '‚Çπ7.1T' },
    { rank: 6, name: 'HDFC Bank', symbol: 'HDFCBANK', price: 1588.20, change: -0.44, market: '‚Çπ11.9T' },
    { rank: 7, name: 'ICICI Bank', symbol: 'ICICIBANK', price: 1185.60, change: 0.38, market: '‚Çπ8.3T' },
    { rank: 8, name: 'TCS', symbol: 'TCS', price: 3835.25, change: 0.54, market: '‚Çπ14.1T' },
  ];

  useEffect(() => {
    const generateChartData = () => {
      const newChartData = {};
      stocks.forEach(stock => {
        const data = [];
        let price = stock.price;
        for (let i = 0; i < 15; i++) {
          const change = (Math.random() - 0.5) * 0.5;
          price = Math.max(stock.price * 0.95, price + change);
          data.push({
            time: i,
            price: parseFloat(price.toFixed(2))
          });
        }
        newChartData[stock.symbol] = data;
      });
      setChartDataMap(newChartData);
    };

    const updatePrices = () => {
      setMarketData(stocks.map(stock => ({
        ...stock,
        price: (stock.price * (1 + (Math.random() - 0.5) * 0.01)).toFixed(2),
        change: (stock.change + (Math.random() - 0.5) * 0.1).toFixed(2)
      })));
    };

    generateChartData();
    updatePrices();
    const interval = setInterval(updatePrices, 5000);
    return () => clearInterval(interval);
  }, []);

  return (
    <section className="section market" aria-label="market update" data-section>
      <div className="container">
        <div className="title-wrapper">
          <h2 className="h2 section-title">Market Overview</h2>
          <a href="#" className="btn-link">See All Stocks</a>
        </div>

        <div className="market-tab">
          <ul className="tab-nav">
            {['View All', 'Tech', 'Banking', 'Energy', 'Pharma', 'Auto', 'FMCG'].map((tab) => (
              <li key={tab}>
                <button 
                  className={`tab-btn ${activeTab === tab ? 'active' : ''}`}
                  onClick={() => setActiveTab(tab)}
                >
                  {tab}
                </button>
              </li>
            ))}
          </ul>

          <table className="market-table">
            <thead className="table-head">
              <tr className="table-row table-title">
                <th className="table-heading"></th>
                <th className="table-heading">#</th>
                <th className="table-heading">Name</th>
                <th className="table-heading">Last Price</th>
                <th className="table-heading">24h %</th>
                <th className="table-heading">Chart</th>
                <th className="table-heading">Market Cap</th>
                <th className="table-heading"></th>
              </tr>
            </thead>
            <tbody className="table-body">
              {marketData.map((stock) => (
                <tr className="table-row" key={stock.symbol}>
                  <td className="table-data">
                    <button className="add-to-fav" aria-label="Add to favourite">‚ô°</button>
                  </td>
                  <th className="table-data rank">{stock.rank}</th>
                  <td className="table-data">
                    <div className="wrapper">
                      <h3>
                        <a href="#" className="coin-name">
                          {stock.name} <span className="span">{stock.symbol}</span>
                        </a>
                      </h3>
                    </div>
                  </td>
                  <td className="table-data last-price">${stock.price}</td>
                  <td className={`table-data last-update ${stock.change >= 0 ? 'green' : 'red'}`}>
                    {stock.change >= 0 ? '+' : ''}{stock.change}%
                  </td>
                  <td className="table-data market-chart">
                    {chartDataMap[stock.symbol] && (
                      <ResponsiveContainer width={80} height={40}>
                        <LineChart data={chartDataMap[stock.symbol]}>
                          <Line type="monotone" dataKey="price" stroke={stock.change >= 0 ? '#16a34a' : '#dc2626'} dot={false} isAnimationActive={false} />
                        </LineChart>
                      </ResponsiveContainer>
                    )}
                  </td>
                  <td className="table-data market-cap">{stock.market}</td>
                  <td className="table-data">
                    <button className="btn btn-outline">Details</button>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </section>
  );
}
